.attributes-container {
  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px; 
}

.primary-attributes, .secondary-attributes {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr)); 
  grid-gap: 8px; 
  gap: 8px; 
}

