}


.network {
  position: relative;
  display: flex;
  align-items: center;
  height: 48px;
  padding: 6px 16px;
  background-color: var(--color-8);
  border-radius: 16px;
  font-size: 14px;
  line-height: 21px;
  cursor: pointer;
  color: #fff;
}

.network img {
  width: 20px;
  height: 20px;
  padding: 14px;
}

.network svg {
  width: 20px;
  height: 20px;
  margin-right: 14px;
  vertical-align: middle;
  display: inline-block;
  margin-top: -2px;
}

.network:before {
  content: '';
  display: block;
  width: 100%;
  height: 32px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.transitionFade {
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  padding: 10px; /* Adjust padding as needed */
}

.transitionFade.transitionFadeOpen {
  opacity: 1;
}

.transitionFade.transitionFadeClosed {
  opacity: 0;
}


.network.connected {
  justify-content: flex-start;
}

.network.connected:before {
  display: none;
}

.network .item {
  background: #616161;
  border-radius: 16px;
  color: #fff;
}

.network .item:hover {
  background: var(--dark-blue);
  color: #fff;
}


.networkSelect {
  position: absolute;
  right: 0;
  background-color: var(--color-8);
  border-radius: 16px;
  font-size: 14px;
  line-height: 21px;
  padding: 6px 16px;
  cursor: pointer;
  z-index: 2; /* Ensure this is above other modal content */
  color: #fff;
}

.networkSelect .item {
  background: var(--color-8);
  border-radius: 16px;
  padding: 8px;
  margin-bottom: 4px;
}

.networkSelect .item:last-child {
  margin-bottom: 0;
}

.networkSelect .item:hover {
  background: var(--color-7);
}

